import React, { Component } from 'react'
import { connect } from 'react-redux'
// import PropTypes from 'prop-types'
import './ThemDonHang.scss';
import { isEmpty } from 'lodash';
import { Col, Row, } from 'reactstrap';
import { KhachHang as KhachHangComponent, SanPhamIn, TaoDonHang } from './components';
import { getDate, getOrderId, addOrder, getTodayDetail, addCustomer } from 'services/SacMauMoiService';
import { DonHang, SanPham, KhachHang } from 'models';
import { makePDF } from 'services/MakePDF/MakePDF';
import { getDonHangs, getCustomers, getCustomersPhones } from 'reduxStore/sacMauMoiReducer';
import { toast } from 'react-toastify';
import { WRONG_PHONE_NUMBER } from 'constants/Constants';

export class ThemDonHang extends Component {
  static propTypes = {
  }

  state = {
    // is: false
  }

  componentWillMount() {
    const donhang = new DonHang();
    const sanpham = new SanPham();
    const khachhang = new KhachHang();
    this.setState({ donhang, sanpham, khachhang })
  }

  componentDidMount() {
    this.initDonHang();
  }

  initDonHang = async () => {
    const donhang = new DonHang();
    donhang.maDonHang = await getOrderId({ date: new Date() });
    donhang.ngayNhan = getDate({ date: new Date() });
    donhang.maNgay = getDate({ date: new Date() });
    donhang.ngayGiao = '';
    this.setState({ donhang })
  }

  onChangeValue = (e, { type }) => {
    let value = {
      donhang: this.state.donhang,
      khachhang: this.state.khachhang,
      sanpham: this.state.sanpham,
    }
    this.setState({
      [type]: {
        ...value[type],
        [e.target.name]: e.target.value,
      }
    })
  }

  onSelectKhachHang = ({ khachhang }) => {
    this.setState({ khachhang });
  }

  onChangeModel = ({ type, value }) => {
    this.setState({ [type]: value });
  }

  onCheck = (e) => {
    const donhang = this.state.donhang;
    this.setState({
      donhang: {
        ...donhang,
        [e.target.name]: !donhang[e.target.name],
      }
    })
  }

  onAddSanPham = ({ sanpham }) => {
    const { donhang } = this.state;
    donhang.sanPhams.unshift(sanpham);
    donhang.tongTien += sanpham.thanhTien;
    this.setState({
      donhang: {
        ...donhang,
        sanPhams: donhang.sanPhams
      },
      sanpham: new SanPham(),
    })
  }

  onClickHuy = () => { this.setState({ sanpham: new SanPham() }) }


  onClickSua = ({ index }) => {
    const { donhang: { sanPhams } } = this.state;
    const sanpham = sanPhams[index];
    this.setState({ sanpham })
    this.onClickXoa({ index });
  }

  onClickXoa = ({ index }) => {
    const { donhang } = this.state;
    const tienCanXoa = donhang.sanPhams[index].thanhTien;
    const newSanPhams = donhang.sanPhams.filter((sanpham, mapIndex) => {
      return mapIndex !== index
    })
    donhang.tongTien = donhang.tongTien - tienCanXoa;
    this.setState({
      donhang: { ...donhang, sanPhams: newSanPhams }
    })
  }

  checkValidToFinish = () => {
    const { donhang: { ngayNhan, sanPhams } } = this.state;
    const { khachhang: { congTy, soDienThoai } } = this.state;

    if (isEmpty(ngayNhan) || isEmpty(sanPhams)
      || isEmpty(congTy) || isEmpty(soDienThoai)
    ) {
      return true;
    }


    return false;
  }

  automationAddCustomer = async ({ khachhang }) => {
    const congTyCheck = this.props.customers[khachhang.congTy];
    const phoneCheck = this.props.customersPhones[khachhang.soDienThoai];
    console.log(this.props.customersPhones)
    if (!congTyCheck && !phoneCheck) {
      for (let i = 0; i < khachhang.soDienThoai.length; i++) {
        const character = khachhang.soDienThoai[i];
        if ('0123456789 '.indexOf(character) === -1) {
          toast.error(WRONG_PHONE_NUMBER)
          return;
        }
      }
      addCustomer({ khachhang, type: 'soDienThoai' });
      addCustomer({ khachhang, type: 'congTy' });
      this.props.getCustomers();
      this.props.getCustomersPhones();
      return true;
    } else if (!congTyCheck && phoneCheck) {
      toast.error("Số điện thoại đã tồn tại");
      return false;
    } else if (congTyCheck && !phoneCheck) {
      toast.error("Tên công ty đã tồn tại");
      return false;
    }

    return true;
  }

  onPrint = async () => {
    const { donhang: { tongTien, tamUng }, khachhang } = this.state;
    const donhang = { ...this.state.donhang };
    const date = new Date();
    const today = getTodayDetail();
    donhang.maDonHang = await getOrderId({ date });
    donhang.conLai = Number(tongTien) - Number(tamUng);
    donhang.khachhang = khachhang;
    donhang.createAt = today.time;
    donhang.modifiedAt = today.time;
    donhang.month = today.month;
    donhang.year = today.year;
    donhang.sanPhams.forEach(sanPham => {
      donhang.sanPhamsName += `${sanPham.tenSanPham}, `;
    })
    this.printPDF({ donhang, khachhang });
  }

  onLuuAndPrint = async () => {
    const { firebase: { auth: { currentUser: { email } } } } = this.props;
    const { donhang: { tongTien, tamUng }, khachhang } = this.state;
    const donhang = { ...this.state.donhang };
    const date = new Date();
    const today = getTodayDetail();
    donhang.maDonHang = await getOrderId({ date });
    donhang.conLai = Number(tongTien) - Number(tamUng);
    donhang.khachhang = khachhang;
    donhang.createAt = today.time;
    donhang.modifiedAt = today.time;
    donhang.modifiedBy = email;
    donhang.month = today.month;
    donhang.year = today.year;
    donhang.sanPhams.forEach(sanPham => {
      donhang.sanPhamsName += `${sanPham.tenSanPham}, `;
    })

    // kiểm tra sự tồn tại customer đã có hay chưa. Chưa -> thêm mới, Rổi -> Nothing
    const isAddCustomer = await this.automationAddCustomer({ khachhang });
    if (!isAddCustomer) return;

    //
    const check = window.confirm('Thêm đơn hàng!')
    if (!check) return;
    this.printPDF({ donhang, khachhang });
    addOrder({ donhang });
    this.initDonHang();
  }

  printPDF = ({ donhang, khachhang }) => {
    makePDF({ donhang, khachhang });
  }

  huyCustomer = () => {
    this.setState({ khachhang: new KhachHang() })
  }

  render() {
    const isValidToFinish = this.checkValidToFinish();
    return (
      <Row className="mt-3">
        <Col lg="6" md="12">
          <KhachHangComponent
            donhang={this.state.donhang}
            khachhang={this.state.khachhang}
            onChangeValue={this.onChangeValue}
            onChangeModel={this.onChangeModel}
            huyCustomer={this.huyCustomer}
            onSelectKhachHang={this.onSelectKhachHang}
          />
        </Col>

        <Col lg="6" md="12">
          <TaoDonHang
            donhang={this.state.donhang}
            sanpham={this.state.sanpham}
            onChangeValue={this.onChangeValue}
            onAddSanPham={this.onAddSanPham}
            onClickHuy={this.onClickHuy}
            isValidToFinish={isValidToFinish}
            onPrint={this.onPrint}
            onLuuAndPrint={this.onLuuAndPrint}
            onChangeModel={this.onChangeModel}
            onCheck={this.onCheck}
          />
        </Col>

        <Col lg="12" md="12">
          <SanPhamIn
            donhang={this.state.donhang}
            onClickXoa={this.onClickXoa}
            onClickSua={this.onClickSua}
            onChangeModel={this.onChangeModel}
          />
        </Col>
      </Row>
    )
  }
}

const mapStateToProps = state => {
  const { customers, customersPhones } = state.sacMauMoi;
  return { customers, customersPhones }
}

export default connect(mapStateToProps, { getCustomers, getDonHangs, getCustomersPhones })(ThemDonHang)
