import React, { Component } from 'react'
import { isEmpty, forEach } from 'lodash'
import { connect } from 'react-redux';
import { toast } from 'react-toastify';
// import PropTypes from 'prop-types'
import {
  Row, Col,
  Button, Card, CardBody, CardHeader,
  FormGroup, Input, Label,
} from 'reactstrap';
import { RequiredText, ModalChooseCustomer } from 'components/_shared';
import { addCustomer, getCustomer, checkExistedCustomer, checkExistedCustomerToUpdate } from 'services/SacMauMoiService';
import { getCustomers, getCustomersPhones } from 'reduxStore';
import {
  THEM_THANH_CONG,
  UPDATE_THANH_CONG,
  UPDATE_WARNING,
  // NOT_ENTER_PHONE_NUMBER,
  WRONG_PHONE_NUMBER,
  NOT_EXISTED_PHONE_NUMBER
} from '../../../../constants/Constants';

class KhachHang extends Component {
  static propTypes = {

  }

  state = {
    showModal: false,
    // showModal: true,
  }

  handleOpenModal = () => {
    this.setState({ showModal: true });
  }

  handleCloseModal = () => {
    this.setState({ showModal: false });
  }

  onChangeKhachHang = (e) => {
    const { onChangeValue, customers, customersPhones, onSelectKhachHang } = this.props;
    const value = e.target.value;
    const name = e.target.name;
    onChangeValue(e, { type: 'khachhang' });
    if (name === 'congTy' && customers && customers[value]) {
      const khachhang = customers[value];
      onSelectKhachHang({ khachhang })
    }

    if (name === 'soDienThoai' && customersPhones && customersPhones[value]) {
      const khachhang = customersPhones[value];
      onSelectKhachHang({ khachhang })
    }
  }

  onChangeDonHang = (e) => {
    const { onChangeValue } = this.props;
    onChangeValue(e, { type: 'donhang' })
  }

  getcustomerByCongTy = () => {
    // const { khachhang: { congTy, soDienThoai } } = this.props;
    // if()
  }

  getCustomerByPhone = async ({ soDienThoai }) => {
    const { onChangeModel } = this.props;
    const khachhang = await getCustomer({ type: 'soDienThoai', value: soDienThoai });
    if (!khachhang) {
      // alert('Số điện thoại khách hàng chưa tồn tại!');
      toast.error(NOT_EXISTED_PHONE_NUMBER)
      return;
    }
    onChangeModel({ type: 'khachhang', value: khachhang })
  }

  addCustomerHandle = () => {
    const { khachhang } = this.props;
    const { getCustomers, getCustomersPhones } = this.props;
    if (isEmpty(khachhang.soDienThoai)) {
      alert('Chưa nhập số điện khách hàng.')
      return;
    }

    if (isEmpty(khachhang.congTy)) {
      alert('Chưa nhập tên công ty.')
      return;
    }

    for (let i = 0; i < khachhang.soDienThoai.length; i++) {
      const character = khachhang.soDienThoai[i];
      if ('0123456789 '.indexOf(character) === -1) {
        toast.error(WRONG_PHONE_NUMBER)
        return;
      }
    }

    const isComfirm = window.confirm('Thêm khách hàng mới?')
    if (!isComfirm) return;
    checkExistedCustomer({ khachhang }).then(res => {
      if (res.result === true) {
        res.messages.map(message => {
          toast.error(message, { autoClose: 4000 });
          return null;
        })
        return;
      }

      addCustomer({ khachhang, type: 'soDienThoai' })
      addCustomer({ khachhang, type: 'congTy' })
      toast.success(THEM_THANH_CONG);

      getCustomers();
      getCustomersPhones();
    })
  }

  updateCustomer = () => {
    const { khachhang, getCustomers } = this.props;
    checkExistedCustomerToUpdate({ khachhang }).then(res => {
      if (res.result === true) {
        addCustomer({ khachhang, type: 'soDienThoai' })
        addCustomer({ khachhang, type: 'congTy' })
        getCustomers();
        toast.success(UPDATE_THANH_CONG);
      } else {
        toast.error(UPDATE_WARNING, { autoClose: 4000 });
      }
    })
  }

  huyCustomer = () => {
    this.props.huyCustomer();
  }

  checkValidAddCustomer = () => {
    const { khachhang: { congTy, soDienThoai } } = this.props;
    if (isEmpty(congTy) || isEmpty(soDienThoai)) {
      return true;
    }
    return false;
  }

  renderCustomerDatalist = () => {
    const { customers } = this.props;
    const result = [];
    if (customers) {
      forEach(customers, (customer) => {
        result.push(<option key={customer.congTy} value={customer.congTy} />)
      })
    }
    return result;
  }

  componentDidMount() {
    this.props.getCustomers();
    this.props.getCustomersPhones();
  }

  onChangeDonGia = (e) => {
    console.log(e.target.value)
  }

  render() {
    const { khachhang: { congTy, soDienThoai, email, nguoiDatHang, diaChi } } = this.props;
    const { donhang: { ghiChuKhachHang, maDonHang, ngayNhan, ngayGiao } } = this.props;
    const { customers } = this.props;
    const { onChangeModel } = this.props;
    const isValidAddCustomer = this.checkValidAddCustomer();
    return (
      <div>
        <Card>
          <CardHeader>
            <strong>Thông tin khách hàng</strong>
          </CardHeader>
          <CardBody>
            <FormGroup row>
              <Col xs="4">
                <Label htmlFor="name">Mã đơn hàng</Label>
                <Input type="text" name="maDonHang" id="maDonHang" value={maDonHang} disabled />
              </Col>
              <Col xs="4">
                <Label htmlFor="ngayNhan">Ngày nhận <RequiredText /></Label>
                <Input type="date" name="ngayNhan" id="ngayNhan" value={ngayNhan} required
                  onChange={this.onChangeDonGia}
                />
              </Col>
              <Col xs="4">
                <Label htmlFor="ngayGiao">Ngày giao</Label>
                <Input type="date" name="ngayGiao" id="ngayGiao" value={ngayGiao} required
                  onChange={this.onChangeDonGia}
                />
              </Col>
            </FormGroup>

            <hr />

            <Row>
              <Col lg="6">
                <FormGroup>
                  <Label htmlFor="congTy">
                    Khách Hàng <RequiredText />
                    <Button size="sm" color="success" className="ml-2"
                      onClick={this.handleOpenModal}
                    >Chọn</Button>
                  </Label>
                  <Input type="text" name="congTy" id="congTy" placeholder="Tên công ty" tabIndex={1}
                    value={congTy} onChange={this.onChangeKhachHang} list="customers"
                  />
                  <datalist id="customers">{this.renderCustomerDatalist()}</datalist>
                </FormGroup>
              </Col>

              <Col lg="6">
                <FormGroup>
                  <Label htmlFor="soDienThoai">Điện Thoại <RequiredText />
                    <Button size="sm" color="success" className="ml-2" onClick={() => this.getCustomerByPhone({ soDienThoai })}>Kiểm tra</Button>
                  </Label>
                  <Input type="phone" name="soDienThoai" id="soDienThoai" placeholder="số điện thoại"
                    value={soDienThoai} onChange={this.onChangeKhachHang}
                    tabIndex={2}
                    maxLength="13"
                  />
                </FormGroup>
              </Col>
            </Row>
            <Row>
              <Col lg="6">
                <FormGroup>
                  <Label htmlFor="nguoiDatHang">Người Đặt Hàng</Label>
                  <Input type="text" name="nguoiDatHang" id="nguoiDatHang" placeholder="Tên người đặt hàng"
                    value={nguoiDatHang} onChange={this.onChangeKhachHang}
                    tabIndex={3}
                  />
                </FormGroup>
              </Col>
              <Col lg="6">
                <FormGroup>
                  <Label htmlFor="email">Email</Label>
                  <Input type="email" name="email" id="email" placeholder="email@gmail.com"
                    value={email} onChange={this.onChangeKhachHang}
                    tabIndex={4}
                  />
                </FormGroup>
              </Col>
            </Row>
            <Row>
              <Col lg="12">
                <FormGroup>
                  <Label htmlFor="diaChi">Địa chỉ</Label>
                  <Input type="text" name="diaChi" id="diaChi" placeholder="Địa chỉ"
                    value={diaChi} onChange={this.onChangeKhachHang}
                    tabIndex={5}
                  />
                </FormGroup>
              </Col>
            </Row>

            <FormGroup>
              <Button
                disabled={isValidAddCustomer}
                color="primary"
                className="mr-2"
                onClick={this.addCustomerHandle}
              ><i className="fa fa-plus fa-lg"></i> Khách Hàng</Button>
              <Button
                disabled={isValidAddCustomer}
                outline
                color="info"
                id="updateCustomer"
                className="mr-2"
                onClick={this.updateCustomer}>Cập nhật</Button>
              <Button

                outline
                color="warning"
                id="huyXoaHang"
                className="mr-2"
                onClick={this.huyCustomer}>Hủy</Button>
            </FormGroup>

            <hr />

            <Row>
              <Col lg="12">
                <Label htmlFor="ghichu">Ghi chú khách hàng</Label>
                <textarea className="form-control"
                  rows='2'
                  type="text" name="ghiChuKhachHang" id="ghiChuKhachHang" placeholder="ghi chú khách hàng"
                  value={ghiChuKhachHang} onChange={this.onChangeDonHang}
                />
              </Col>
            </Row>
          </CardBody>
        </Card>

        <ModalChooseCustomer
          customers={customers}
          getCustomerByPhone={this.getCustomerByPhone}
          isOpen={this.state.showModal}
          close={this.handleCloseModal}
          chooseCustomer={this.onChooseCustomer}
          onChangeModel={onChangeModel}
        />
      </div>
    )
  }
}

const mapStateToProps = state => {
  const { customers, customersPhones } = state.sacMauMoi;
  return { customers, customersPhones }
}

export default connect(mapStateToProps, {
  getCustomers, getCustomersPhones
})(KhachHang)
