import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { isEmpty } from 'lodash'
import { RequiredText } from 'components/_shared';
import {
  Button, Card, CardBody, CardHeader, Col,
  FormGroup, Input, Label, Row
} from 'reactstrap';
import { formatMoney } from 'services/SacMauMoiService';

export class TaoDonHang extends Component {
  static propTypes = {
    donhang: PropTypes.object.isRequired,
    onChangeValue: PropTypes.func.isRequired,
    onAddSanPham: PropTypes.func.isRequired,
  }

  state = {

  }

  onChangeDonGia = (e) => {
    const { onChangeValue } = this.props;
    onChangeValue(e, { type: 'donhang' });
  }

  onChangeSanPham = (e) => {
    const { onChangeValue } = this.props;
    onChangeValue(e, { type: 'sanpham' });
  }

  onChangeDonHang = (e) => {
    const { onChangeValue } = this.props;
    onChangeValue(e, { type: 'donhang' })
  }

  onClickAdd = () => {
    const { sanpham: { soLuong, donGia } } = this.props;
    const { sanpham, onAddSanPham } = this.props;
    sanpham.thanhTien = Number(soLuong) * Number(donGia);
    onAddSanPham({ sanpham });
  }

  onClickHuy = () => {
    this.props.onClickHuy();
  }

  checkValidAddOrder = () => {
    const { sanpham: { tenSanPham, donViTinh, soLuong, donGia } } = this.props;
    if (isEmpty(tenSanPham) || isEmpty(donViTinh) || isEmpty((soLuong)) || isEmpty((donGia))) {
      return false;
    }
    return true;
  }

  render() {
    const { donhang: { inNhanh, tongTien, tamUng }, isValidToFinish } = this.props;
    const { sanpham: { tenSanPham, donViTinh, soLuong, donGia, moTa } } = this.props;
    const { onPrint, onLuuAndPrint, onCheck } = this.props;
    const conLai = tongTien - tamUng;
    const thanhTien = isEmpty(soLuong) || isEmpty(donGia) ? 0 : formatMoney(Number(soLuong) * Number(donGia));
    const validAddOrder = this.checkValidAddOrder();
    // console.log(this.props.donhang);
    return (
      <Card>
        <CardHeader>
          <strong>Đơn Hàng</strong>
        </CardHeader>
        <CardBody>
          <FormGroup row>
            <Col xs="12"><Label><strong>Thêm sản phẩm</strong> <RequiredText /></Label></Col>
            <Col xs="4" style={{ paddingRight: 0 }}>
              <Label htmlFor="ngaygiao">Tên</Label>
              {/* <Input type="text" name="tenSanPham" id="tenSanPham" placeholder="Nhập tên hàng"
                value={tenSanPham} onChange={this.onChangeSanPham}
              /> */}
              <textarea className="form-control"
                rows='1'
                type="text" name="tenSanPham" id="tenSanPham" placeholder="Nhập tên hàng"
                value={tenSanPham} onChange={this.onChangeSanPham}
              />
            </Col>
            <Col xs="2" style={{ paddingRight: 0 }}>
              <Label htmlFor="ngaygiao">ĐVT</Label>
              <Input type="text" name="donViTinh" id="donViTinh" placeholder="ĐVT"
                value={donViTinh} onChange={this.onChangeSanPham}

              />
            </Col>
            <Col xs="3">
              <Label htmlFor="ngaygiao">SL</Label>
              <Input type="number" name="soLuong" id="soluong" placeholder="số lượng"
                min='0' step='1 '
                value={(soLuong)} onChange={this.onChangeSanPham}
              />
            </Col>
            <Col xs="3">
              <Label htmlFor="ngaygiao">Đơn giá</Label>
              <Input type="number" name="donGia" id="dongia" placeholder="Đơn giá"
                min='0' step='100'
                value={(donGia)} onChange={this.onChangeSanPham}
              />
            </Col>
          </FormGroup>

          <FormGroup row>
            <Col xs='8'>
              <Label htmlFor="ghichu">Mô tả sản phầm</Label>
              <textarea className="form-control"
                rows='4'
                type="text" name="moTa" id="moTa" placeholder="mô tả sản phẩm"
                value={moTa} onChange={this.onChangeSanPham}
              />
            </Col>
            <Col xs='4'>
              <Label htmlFor="options">Options</Label>
              <FormGroup check className="checkbox">
                <Input className="form-check-input" type="checkbox" id="inNhanh" name="inNhanh" value="inNhanh"
                  checked={inNhanh} onChange={onCheck}
                />
                <Label check className="form-check-label" htmlFor="inNhanh">In Nhanh</Label>
              </FormGroup>
              {/* <FormGroup check className="checkbox">
                <Input className="form-check-input" type="checkbox" id="xuatHoaDon" name="xuatHoaDon" value="xuatHoaDon"
                  checked={xuatHoaDon} onChange={onCheck}
                />
                <Label check className="form-check-label" htmlFor="xuatHoaDon">Xuất hoá đơn</Label>
              </FormGroup> */}
            </Col>
          </FormGroup>

          <FormGroup>
            <Button disabled={!validAddOrder} color="primary" className="mr-2"
              onClick={this.onClickAdd}
            >
              <i className="fa fa-plus fa-lg"></i> Thêm
            </Button>
            <Button outline disabled={!validAddOrder} color="danger" className="mr-2"
              onClick={this.onClickHuy}
            >Huỷ</Button>
            <span><strong>Thành tiền: {thanhTien}</strong></span>
          </FormGroup>

          <hr />

          <Row>
            <Col lg="6">
              <FormGroup row>
                <Col xs="4"><Label htmlFor="tongTien">Tổng tiền</Label></Col>
                <Col xs="8">
                  <Input disabled type="text" name="tongTien" id="tongTien" placeholder="tổng tiền đơn hàng"
                    value={formatMoney(tongTien)}
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col xs="4"><Label htmlFor="tamUng">Tạm ứng</Label></Col>
                <Col xs="8">
                  <Input type="number" name="tamUng" id="tamUng" placeholder="tạm ứng từ khách hàng"
                    value={tamUng || ""} onChange={this.onChangeDonHang} step="1000"
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col xs="4"><Label htmlFor="conLai">Còn lại</Label></Col>
                <Col xs="8">
                  <Input disabled type="text" name="conLai" id="conLai" placeholder="số tiền còn lại"
                    value={formatMoney(conLai)}
                  />
                </Col>
              </FormGroup>
            </Col>
          </Row>

          <FormGroup style={{ marginBottom: 0 }} row>
            <Col xs='6'>
              <Button disabled={isValidToFinish} block color="success" onClick={onLuuAndPrint}>Lưu và In</Button>
            </Col>
            <Col xs='6'>
              <Button outline disabled={isValidToFinish} block color="success" onClick={onPrint}>
                Xem trước <i className="fa fa-print" aria-hidden="true"></i>
              </Button>
            </Col>
          </FormGroup>
        </CardBody>
      </Card >
    )
  }
}

export default TaoDonHang
