import React from 'react'
import { Button, UncontrolledTooltip, } from 'reactstrap';
import { formatMoney } from 'services/SacMauMoiService';

const PrintItem = (props) => {
  const { sanpham, index, isViewing } = props;
  const { onClickSua, onClickXoa } = props;
  return (
    <tr>
      <td>{sanpham.tenSanPham}</td>
      <td>{`${sanpham.moTa}`}</td>
      <td>{sanpham.donViTinh}</td>
      <td>{formatMoney(sanpham.soLuong)}</td>
      <td>{formatMoney(sanpham.donGia)}</td>
      <td>{formatMoney(sanpham.thanhTien)}</td>
      {isViewing ? null :
        (<td>
          <Button outline size="sm" color="warning" className="mr-2" id="nutSua"
            onClick={() => onClickSua({ index })}
          >
            <i className="fa fa-edit fa-lg" />
          </Button>
          <UncontrolledTooltip placement="bottom" target="nutSua">Sửa</UncontrolledTooltip>

          <Button outline size="sm" color="danger" id="nutXoa"
            onClick={() => onClickXoa({ index })}
          >
            <i className="fa fa-trash fa-lg"></i>
          </Button>
          <UncontrolledTooltip placement="bottom" target="nutXoa">Xoá</UncontrolledTooltip>
        </td>)
      }

    </tr>
  )
}

export default PrintItem
