import React, { Component } from 'react'
// import PropTypes from 'prop-types'
import {
  Card, CardBody, CardHeader, Table,
} from 'reactstrap';
import PrintItem from './PrintItem';

export class SanPhamIn extends Component {
  static propTypes = {

  }

  state = {
  }

  renderPrintItems = () => {
    const { donhang: { sanPhams }, isViewing } = this.props;
    const { onClickSua, onClickXoa, } = this.props;
    return sanPhams.map((sanpham, index) => {
      return (
        <PrintItem
          onClickXoa={onClickXoa}
          onClickSua={onClickSua}
          index={index}
          sanpham={sanpham}
          key={index}
          isViewing={isViewing}
        />)
    })
  }

  render() {
    const { isViewing } = this.props;
    return (
      <Card>
        <CardHeader>
          <strong>Sản phẩm in</strong>
        </CardHeader>
        <CardBody>
          <Table responsive striped>
            <thead>
              <tr>
                <th>Tên sản phẩm</th>
                <th>Mô tả</th>
                <th>ĐVT</th>
                <th>Số lượng</th>
                <th>Đơn giá</th>
                <th>Thành tiền</th>
                {isViewing ? null : <th>Chức năng</th>}

              </tr>
            </thead>
            <tbody>
              {this.renderPrintItems()}
            </tbody>
          </Table>
        </CardBody>

      </Card>
    )
  }
}

export default SanPhamIn
